/** @jsx jsx */
/* eslint no-unused-vars: 0 */
import React, { useState } from 'react';
import { jsx, useThemeUI } from 'theme-ui';
import { Flex, Box, Heading, Text } from 'rebass';
import styled from '@emotion/styled/macro';

import strings from './strings.json';

const StyledBox = styled(Box)`
  position: relative;
`;

const Image = styled.img`
  margin-bottom: 1em;
`;

const Flag = styled(Box)`
  content: "";
  position: absolute;
  background: transparent;
  left: 0px;
  transform: translate(0px, 0px);
  width: inherit;
  height: 0px;
  bottom: 0px;
  border-left: 50vw solid white;
  border-right: 50vw solid white;
`;

const MainPageSiteDescription = (props) => {
  const {
    theme: { breakpoints },
  } = useThemeUI();

  const { siteDesc } = props;
  const sectionItemsNumber =
    siteDesc && siteDesc.children && siteDesc.children.length > 0
      ? siteDesc.children.length
      : 1;
  const sectionHeight = sectionItemsNumber < 3 ? '60vh' : '40vh';

  return (
    <StyledBox bg="background" textAlign="center" mx="auto" px={["5vw", "3.5vw"]}>
      <Flex flexWrap="wrap" bg="backgroundTeal" px={["5vw", "3.5vw"]} pt={["5em", "3em"]} pb={["2em", "3em"]}>
        {siteDesc.children.map((block, index) => {
          if (block.type === 'description') {
            return (
              <Box
                width={[1, 1 / sectionItemsNumber]}
                key={index}
                height={"auto"}
                mb={["4em", "2em"]}
                p={[0, "1em"]}
                textAlign="center"
              >
                <Image src={block.imageSrc} alt={block.handle} />
                <Heading fontSize={["5vw", "2vw", "1.5vw"]} mb=".5em">{block.title}</Heading>
                <Text>{block.description}</Text>
              </Box>
            );
          } else {
            return '';
          }
        })}
      </Flex>

      <Flag
        sx={{
          borderTopWidth: ["3em", "5em"],
          borderTopStyle: 'solid',
          borderTopColor: 'transparent'
        }}
      />
    </StyledBox>
  );
};

export default MainPageSiteDescription;
