/** @jsx jsx */
/* eslint no-unused-vars: 0 */
import React, { useState } from 'react';
import { jsx, useThemeUI } from 'theme-ui';
import { Flex, Box, Heading, Text, Button } from 'rebass';
import ShopifyBackgroundImage from '../../components/ShopifyBackgroundImage';
import styled from '@emotion/styled/macro';

import strings from './strings.json';

const { promoBannerButton } = strings;

const StyledBox = styled(Box)`
  position: relative;
`;

const Image = styled.img``;

const MainPagePromo = (props) => {
  const {
    theme: { breakpoints },
  } = useThemeUI();

  const { promo } = props;
  const sectionItemsNumber =
    promo && promo.children && promo.children.length > 0
      ? promo.children.length
      : 1;
  const sectionHeight = sectionItemsNumber < 3 ? '60vh' : '40vh';

  return (
    <StyledBox mx="auto">
      {promo.children.map((block, index) => {
        if (block.type === 'promo') {
          return (
            <ShopifyBackgroundImage src={block.bgImageSrc} maxSize="800" key={index}>
              <Box textAlign={["center", "left"]} color="background" px={["5vw", "7vw"]} py={["2vw", "1vw"]} sx={{ textShadow: '0 0 20px #323232' }}>
                <Image src={block.imageSrc} alt={block.handle} sx={{ maxWidth: ["35vw", "25vw", "15vw"], mt: "4em", mb: "1em" }}/>
                <Heading width={[1, 3 / 5]} fontSize={["8vw", "5vw"]} mb={[".5em", 0]}>{block.title}</Heading>
                <Text mb="3em">{block.description}</Text>
                <Button mb="2em" variant="primary">{promoBannerButton}</Button>
                <Text mb="1em">{block.note}</Text>
              </Box>
            </ShopifyBackgroundImage>
          );
        } else {
          return '';
        }
      })}
    </StyledBox>
  );
};

export default MainPagePromo;
