/** @jsx jsx */

import React from 'react';
import { jsx } from 'theme-ui';
import { Flex, Box, Heading, Text } from 'rebass';
import styled from '@emotion/styled/macro';

import MainPageCollectionBlock from './MainPageCollectionBlock';
import MainPageProductBlock from './MainPageProductBlock';

const StyledBox = styled(Box)`
  position: relative;
  z-index: 1;
`;

const Decor = styled(Box)`
  content: "";
  background: center/contain no-repeat url('https://cdn.shopify.com/s/files/1/0117/9005/7534/files/bitmap.png');
  position: absolute;
  z-index: 0;
  width: 100%;
  transform: translate(-50%, -50%);
`;

const DecorTilt = styled(Box)`
  content: "";
  background: center/contain no-repeat url('https://cdn.shopify.com/s/files/1/0117/9005/7534/files/fruit2-2.png');
  position: absolute;
  z-index: 0;
  width: 100%;
  transform: translate(-50%, -50%);
`;

const MainPageSection = (props) => {
  const { section, data } = props;
  const sectionItemsNumber =
    section && section.children && section.children.length > 0
      ? section.children.length
      : 1;
  const sectionHeight = sectionItemsNumber < 4 ? '60vh' : '40vh';

  return (
    <Box px={["5vw", "3.5vw"]} mx="auto">
      <Flex flexWrap="wrap">
        {section.children.map((block, index) => {
          if (
            block.type === 'collection' &&
            data.collections.nodes.filter(
              (collection) => collection.handle === block.handle
            )[0]
          ) {
            return (
              <StyledBox bg="background"
                width={[1, 1 / sectionItemsNumber]}
                key={index}
                height="auto"
                px={[0, ".5em", "1em"]}
                py={[0, "2em"]}
                mb={[3, 0]}
              >
                {index == 1
                  ? (
                      <Decor sx={{ height: ['45vw', '18vw'], left: ['20%', 0], top: ['-10%', '60%'] }} />
                    )
                  : index == 2
                  ? (
                      <DecorTilt sx={{ height: ['45vw', '18vw'], left: ['80%', 0], top: ['-10%', '15%'] }} />
                    )
                  : "" 
                }
                <MainPageCollectionBlock 
                  block={block}
                  collection={
                    data.collections.nodes.filter(
                      (collection) => collection.handle === block.handle
                    )[0]
                  }
                  bgColor={block.bgColor ? block.bgColor : undefined}
                  textColor={block.textColor ? block.textColor : undefined}
                  textBgColor={
                    block.textBgColor ? block.textBgColor : undefined
                  }
                  buttonText={block.buttonText ? block.buttonText : undefined}
                  buttonTextColor={
                    block.buttonTextColor ? block.buttonTextColor : undefined
                  }
                  buttonBgColor={
                    block.buttonBgColor ? block.buttonBgColor : undefined
                  }
                  backgroundPosition="50% 0%"
                />
              </StyledBox>
            );
          } else if (
            block.type === 'product' &&
            data.products.nodes.filter(
              (product) => product.handle === block.handle
            )[0]
          ) {
            return (
              <Box
                width={[1, 1 / sectionItemsNumber]}
                key={index}
                height={['60vh', sectionHeight]}
                p={1}
              >
                <MainPageProductBlock
                  block={block}
                  product={
                    data.products.nodes.filter(
                      (product) => product.handle === block.handle
                    )[0]
                  }
                  textColor={block.textColor ? block.textColor : undefined}
                  textBgColor={
                    block.textBgColor ? block.textBgColor : undefined
                  }
                  buttonText={block.buttonText ? block.buttonText : undefined}
                  buttonTextColor={
                    block.buttonTextColor ? block.buttonTextColor : undefined
                  }
                  buttonBgColor={
                    block.buttonBgColor ? block.buttonBgColor : undefined
                  }
                  backgroundPosition="50% 0%"
                />
              </Box>
            );
          } else {
            return '';
          }
        })}
      </Flex>
    </Box>
  );
};

export default MainPageSection;
