/** @jsx jsx */
/* eslint no-unused-vars: 0 */
import React, { useState } from 'react';
import { jsx, useThemeUI } from 'theme-ui';
import { Flex, Box, Heading, Text, Button } from 'rebass';
import {
  CarouselProvider,
  Slider,
  Slide,
  ButtonBack,
  ButtonNext,
  DotGroup,
} from 'pure-react-carousel';
import 'pure-react-carousel/dist/react-carousel.es.css';
import './carousel.css';
import styled from '@emotion/styled/macro';

import ShopifyBackgroundImage from '../../components/ShopifyBackgroundImage';
import ChevronLeft from '../../components/Icons/ChevronLeft';
import ChevronRight from '../../components/Icons/ChevronRight';
import strings from './strings.json';

const { ariaNextButtonLabel, ariaBackButtonLabel, ariaSliderButton } = strings;

const Image = styled.img`
  width: 100%;
`;

const Flag = styled(Box)`
  content: "";
  position: absolute;
  background: transparent;
  left: 0px;
  transform: translate(0px, 0px);
  width: inherit;
  height: 20vw;
  bottom: 0px;
  border-left: 50vw solid white;
  border-right: 50vw solid white;

  & ${Image} {
    position: absolute;
    transform: translate(-50%, -50%);
  }
`;


const MainPageCarousel = (props) => {
  const { carousel } = props;

  const {
    theme: { breakpoints },
  } = useThemeUI();

  let naturalSlideHeightMediaQuery;

  if (typeof window !== `undefined`) {
    naturalSlideHeightMediaQuery =
      window && window.matchMedia(`(max-width: ${breakpoints[0]})`);
    naturalSlideHeightMediaQuery.addEventListener('change', (e) => {});
  }

  const [naturalSlideHeight, setNaturalSlideHeight] = useState(
    naturalSlideHeightMediaQuery && naturalSlideHeightMediaQuery.matches
      ? 180
      : 80
  );

  if (typeof window !== `undefined`) {
    naturalSlideHeightMediaQuery.addEventListener('change', (e) => {
      setNaturalSlideHeight(e.matches ? 180 : 80);
    });
  }

  return (
    <Box>
      <CarouselProvider
        naturalSlideWidth={100}
        naturalSlideHeight={naturalSlideHeight}
        totalSlides={carousel.children.length}
        isPlaying
        infinite
      >
        <Slider>
          {carousel.children.map((slide, index) => {
            if (slide.type === 'slide') {
              return (
                <Slide key={index} index={index}>
                  <ShopifyBackgroundImage
                    src={slide.bgImageSrc}
                    maxSize="5000"
                    textColor={
                      slide.textColor ? slide.textColor : undefined
                    }
                    textBgColor={
                      slide.textBgColor ? slide.textBgColor : undefined
                    }
                    buttonText={
                      slide.buttonText ? slide.buttonText : undefined
                    }
                    buttonTextColor={
                      slide.buttonTextColor
                        ? slide.buttonTextColor
                        : undefined
                    }
                    buttonBgColor={
                      slide.buttonBgColor
                        ? slide.buttonBgColor
                        : undefined
                    }
                  >
                    <Flex flexDirection={["column", "row"]} px={["5vw", "3.5vw"]} pt={["30vw", "20vw"]} pb={["30vw", "5vw"]}>
                      <Box width={[1, 2 / 5]} order={[2, 1]} textAlign={["center", "left"]} ml="auto" sx={{ textShadow: '0 0 3px #323232' }}>
                        <Heading fontSize={["7vw", "5vw"]} mb=".5em" sx={{ textTransform: 'capitalize' }}>{slide.header}</Heading>
                        <Text fontSize={["4vw", "1.5vw"]} mb="2em">{slide.subheader}</Text>
                        <Button variant="primary" to={slide.link} px={["5vw", "3vw"]} py={["3vw", "1vw"]}>{ariaSliderButton}</Button>
                      </Box>
                      <Box width={[3 / 5, 3 / 10]} order={[1, 2]} ml={["auto", 0]} mr="auto" my={["5vw", 0]}>
                        <Image src={slide.imageSrc} alt={slide.handle} />
                      </Box>
                    </Flex>
                  </ShopifyBackgroundImage>
                  
                  <Flag
                    sx={{
                      borderTopWidth: ['10vw', '5vw', '10vw'],
                      borderTopStyle: 'solid',
                      borderTopColor: 'transparent'
                    }}
                  >
                    <Box width={["45vw", "25vw"]} mx="auto" sx={{ position: 'relative'}}>
                      <Image src="//cdn.shopify.com/s/files/1/0117/9005/7534/files/fruit2-3.png" alt="decor" />
                    </Box>
                  </Flag>
                </Slide>
              );
            } else {
              return '';
            }
          })}
        </Slider>

        <ButtonBack
          aria-label={ariaBackButtonLabel}
          sx={{
            display: 'block',
            position: 'absolute',
            transform: 'translateY(-50%)',
            top: ['65vw', '35vw'],
            left: ['5vw', '3.5vw'],
            zIndex: '2',
            border: 0,
            bg: 'transparent',
            color: 'primary',
            py: 2,
            px: 0,
          }}
        >
          <Image src='https://cdn.shopify.com/s/files/1/0117/9005/7534/files/slide-arrow.png' alt='previous' sx={{ width: 'auto', height: ['7vw', '5vw'] }} />
        </ButtonBack>

        <ButtonNext
          aria-label={ariaNextButtonLabel}
          sx={{
            display: 'block',
            position: 'absolute',
            transform: 'translateY(-50%)',
            top: ['65vw', '35vw'],
            right: ['5vw', '3.5vw'],
            zIndex: '2',
            border: 0,
            bg: 'transparent',
            color: 'primary',
            py: 2,
            px: 0,
          }}
        >
          <Image src='https://cdn.shopify.com/s/files/1/0117/9005/7534/files/slide-arrow2.png' alt='next' sx={{ width: 'auto', height: ['7vw', '5vw'] }} />
        </ButtonNext>

        <DotGroup
          sx={{
            display: ['block', 'none' ],
            position: 'relative',
            bottom: '10%',
            left: '50%',
            transform: 'translate(-50%)',
            zIndex: '2',
            border: 0,
            bg: 'transparent',
            color: 'primary',
            px: '3',
            textAlign: 'center',
          }}
        />
      </CarouselProvider>

      
    </Box>
  );
};

export default MainPageCarousel;
