/** @jsx jsx */
/* eslint no-unused-vars: 0 */
import React, { useState } from 'react';
import { jsx, useThemeUI } from 'theme-ui';
import { Flex, Box, Heading, Text, Button } from 'rebass';
import styled from '@emotion/styled/macro';

import strings from './strings.json';

const { giftAdsButton } = strings;

const StyledBox = styled(Box)`
  position: relative;
`;

const Image = styled.img`
  width: 100%;
  transition-property: all;
  transition-duration: 0.6s;
  transition-timing-function: ease-in-out;
  transition-delay: 0s;

  &:hover {
    transform: scale(1.05);
    transition-property: all;
    transition-duration: 0.6s;
    transition-timing-function: ease-in-out;
    transition-delay: 0s;
  }
`;

const MainPageGift = (props) => {
  const {
    theme: { breakpoints },
  } = useThemeUI();

  const { gift } = props;
  const sectionItemsNumber =
    gift && gift.children && gift.children.length > 0
      ? gift.children.length
      : 1;
  const sectionHeight = sectionItemsNumber < 3 ? '60vh' : '40vh';

  return (
    <StyledBox bg="background" mx="auto" py="5em" px={["1em", "3.5vw"]} py="5em">
      {gift.children.map((block, index) => {
        if (block.type === 'giftAds') {
          return (
            <Flex
              flexDirection={["column", "row"]}
              key={index}
              height={"auto"}
              p={[0, "1em"]}
              textAlign="center"
            >
              <Box width={[1, 1 / 2]} mr="auto">
                <Image src={block.imageSrc} alt={block.handle} />
              </Box>
              <Box width={[1, 2 / 5]} mr="auto" textAlign={["center", "left"]}>
                <Text fontSize={["2em", "3vw"]} mt={[".5em", 0]} mb={[".5em", ".2em"]}>{block.title}</Text>
                <Text fontSize={["1em", "1.5vw"]} mb="2em">{block.description}</Text>
                <Button variant="primary" px="3vw" py="1vw">{giftAdsButton}</Button>
              </Box>
            </Flex>
          );
        } else {
          return '';
        }
      })}
    </StyledBox>
  );
};

export default MainPageGift;
