import React from 'react';
import { Link as GatsbyLink } from 'gatsby';
import { jsx } from 'theme-ui';
import styled from '@emotion/styled/macro';
import { Flex, Box, Text, Heading, Button } from 'rebass';

import ShopifyBackgroundImage from '../../components/ShopifyBackgroundImageCustom';
import substrDescription from '../../utils/substrDescription';

const StyledBox = styled(Box)`
  position: relative;
  z-index: 2;
  width: inherit;
`;

const TitleBox = styled(Box)`
  position: absolute;
  z-index: 3;
  transform: translateX(-50%);
  left: 50%;
  bottom: 5%;
  width: 100%;
`;

const MainPageCollectionBlock = (props) => {
  const {
    title,
    description,
    image,
    fields: { shopifyThemePath },
  } = props.collection;

  const {
    block,
    bgColor = 'background',
    textColor = 'primary',
    textBgColor = 'white',
    buttonText = 'Shop now',
    buttonTextColor = 'primary',
    buttonBgColor = 'transparent',
  } = props;

  return (
    <Flex
      m="auto"
      p="1"
      textAlign="center"
      sx={{
        position: 'relative',
        zIndex: 1,
        justifyContent: 'flex-start',
        alignItems: 'stretch',
        width: '100%',
        height: 'auto',
        bg: bgColor,
      }}
    >
      <StyledBox p={["15vw", 0]} sx={{ transform: ['translateY(-10%)', 'translateY(-20%)'] }}>
        <ShopifyBackgroundImage src={image.src} maxSize="800" />
      </StyledBox>
      <TitleBox m="auto">
        <GatsbyLink
          to={shopifyThemePath}
          sx={{
            color: textColor,
            textAlign: 'center',
            textDecoration: 'none',
            ':hover,:focus,.active': {
              color: textColor,
              textDecoration: 'none',
            },
          }}
        >
          <Heading
            as="h2"
            fontSize={["9vw", "3vw"]}
            sx={{ textTransform: 'capitalize', textShadow: '0 0 3px #323232' }}
          >
            {block.name ? block.name : title}
          </Heading>
          <Button
            variant="shopNow"
            sx={{
              bg: buttonBgColor,
              color: buttonTextColor,
              textShadow: '0 0 3px #323232',
              fontSize: ["3vw", "1vw"]
            }}
            m="auto"
            py={[2, 2, 3]}
          >
            {buttonText}
          </Button>
        </GatsbyLink>
      </TitleBox>
    </Flex>
  );
};

export default MainPageCollectionBlock;
