import React from 'react';
import { Box } from 'rebass';
import { useStaticQuery, graphql } from 'gatsby';

import MainPageGift from './MainPageGift';
import MainPagePromo from './MainPagePromo';
import MainPageHeader from './MainPageHeader';
import MainPageSection from './MainPageSection';
import MainPageInstagram from './MainPageInstagram';
import MainPageCarousel from './MainPageCustomCarousel';
import MainPageCollectionTab from './MainPageCollectionTab';
import MainPageCollectionBlock from './MainPageCollectionBlock';
import MainPageSiteDescription from './MainPageSiteDescription';
import MainPageFeaturedCollectionBlock from './MainPageFeaturedCollectionBlock';

const MainPage = (props) => {
  const dataQuery = useStaticQuery(graphql`
    query MainPageStaticQuery {
      site {
        siteMetadata {
          gatsbyStorefrontConfig {
            mainPage {
              handle
              type
              name
              description
              limit
              isExpanded
              textBgColor
              textColor
              buttonText
              buttonTextColor
              buttonBgColor
              children {
                handle
                type
                name
                title
                description
                header
                subheader
                imageSrc
                bgImageSrc
                note
                limit
                link
                isExpanded
                bgColor
                textBgColor
                textColor
                buttonText
                buttonTextColor
                buttonBgColor
              }
            }
            socialNetworks
          }
        }
      }
    }
  `);

  const { mainPage, socialNetworks } = dataQuery.site.siteMetadata.gatsbyStorefrontConfig;

  const { data } = props;

  const { cartUrl } = props.pageContext;

  return (
    <Box bg="background" mx="auto">
      {mainPage.map((block, index) => {
        if (block.type === 'carousel') {
          return (
            <Box width={1} key={index}>
              <MainPageCarousel carousel={block} data={data} />
            </Box>
          );
        } else if (block.type === 'section') {
          return (
            <Box width={1} key={index}>
              <MainPageSection section={block} data={data} />
            </Box>
          );
        } else if (
          block.type === 'product' ||
          (block.type === 'collection' && block.isExpanded === false)
        ) {
          return (
            <Box width={1} key={index}>
              <MainPageSection section={{ children: [block] }} data={data} />
            </Box>
          );
        } else if (block.type === 'collection' && block.isExpanded == true) {
          let products = [];
          props.data.feautiredCollections.nodes.forEach((node) => {
            if (node.handle === block.handle) {
              products = [...products, ...node.products];
            }
          });
          return (
            <MainPageFeaturedCollectionBlock
              block={block}
              products={products}
              cartUrl={cartUrl}
              key={index}
            />
          );
        } else if (block.type === 'tabs') {
          let products = [];
          props.data.feautiredCollections.nodes.forEach((node) => {
            if (node.handle === block.handle) {
              products = [...products, ...node.products];
            }
          });
          return (
            <Box width={1} key={index}>
              <MainPageCollectionTab
                tabs={block}
                products={products}
                cartUrl={cartUrl}
                key={index}
              />
            </Box>
          );
        } else if (block.type === 'promo') {
          return (
            <Box width={1} key={index}>
              <MainPagePromo promo={block} />
            </Box>
          );
        } else if (block.type === 'gift') {
          return (
            <Box width={1} key={index}>
              <MainPageGift gift={block} />
            </Box>
          );
        } else if (block.type === 'siteDesc') {
          return (
            <Box width={1} key={index}>
              <MainPageSiteDescription siteDesc={block} />
            </Box>
          );
        } else if (block.type === 'instagram') {
          return (
            <Box width={1} key={index}>
              <MainPageInstagram instagram={block} socialNetworks={socialNetworks} />
            </Box>
          );
        } else if (block.type === 'header') {
          return (
            <Box width={1} key={index}>
              <MainPageHeader header={block} />
            </Box>
          );
        } else {
          return '';
        }
      })}
    </Box>
  );
};

export default MainPage;
