/** @jsx jsx */
/* eslint no-unused-vars: 0 */
import React, { useState } from 'react';
import { jsx, useThemeUI } from 'theme-ui';
import { Flex, Box, Heading, Text } from 'rebass';
import styled from '@emotion/styled/macro';

const MainPageHeader = (props) => {
  const {
    theme: { breakpoints },
  } = useThemeUI();

  const { header } = props;
  const sectionItemsNumber =
    header && header.children && header.children.length > 0
      ? header.children.length
      : 1;
  const sectionHeight = sectionItemsNumber < 3 ? '60vh' : '40vh';

  return (
    <Box bg="background" textAlign="center" mx="auto" p={["5vw", "3.5vw"]}>
      <Flex flexWrap="wrap">
        {header.children.map((block, index) => {
          return (
            <Box
              bg="background"
              width={[1, 1 / sectionItemsNumber]}
              key={index}
              height={"auto"}
              p={[".3em", "1em"]}
              textAlign="center" sx={{ textShadow: '0 0 2px #323232' }}
            >
              <Text width={[1, "40%"]} fontSize={["7vw", "3vw"]} mx="auto">{block.header}</Text>
              <Text width={["95%", "30%"]} fontSize={["4vw", "1.3vw"]} mx="auto" my="1em">{block.subheader}</Text>
            </Box>
          );
        })}
      </Flex>
    </Box>
  );
};

export default MainPageHeader;
