/** @jsx jsx */
/* eslint no-unused-vars: 0 */
import React, { useState } from 'react';
import { useStaticQuery, graphql } from "gatsby";
import { jsx, useThemeUI } from 'theme-ui';
import { Flex, Box, Heading, Text, Button } from 'rebass';
import styled from '@emotion/styled/macro';
import strings from './strings.json';

import CatalogProducts from '../catalog/CatalogProducts';
import CollectionTab from './CollectionTab';
import Tabs from 'react-bootstrap/Tabs';
import Tab from 'react-bootstrap/Tab';

const { ariaTabHeader } = strings;

const MainPageCollectionTab = (props) => {
  const data = useStaticQuery(graphql`
    {
      allShopifyCollection {
        nodes {
          handle
          title
          products {
            handle
            title
            fields {
              shopifyThemePath
            }
            images {
              altText
              originalSrc
            }
            priceRange {
              minVariantPrice {
                amount
                currencyCode
              }
              maxVariantPrice {
                amount
                currencyCode
              }
            }
            variants {
              availableForSale
              compareAtPrice
            }
          }
        }
      }
    }
  `);

  const {
    theme: { breakpoints },
  } = useThemeUI();

  const { tabs, products, limit, skip, cartUrl } = props;

  return (
    <Box m={["5vw", "3.5vw"]} mb={0} p={["5vw", "3.5vw"]} bg="backgroundGrey" textAlign="center">
      <Heading sx={{ lineHeight: 'heading', textTransform: 'capitalize' }}>{ariaTabHeader}</Heading>
      <Tabs defaultActiveKey="face">
        {tabs.children.map((block, index) => {
          if (block.type === 'collection') {
            return (
              <Tab eventKey={block.handle} title={block.name} key={index}>
                {data.allShopifyCollection.nodes.filter(
                  (collection) => collection.handle === block.handle)[0]
                  ? <Box key={index}>
                      {block.handle}
                    </Box>
                  : '' }
              </Tab>
            );
          } else {
            return '';
          }
        })}
      </Tabs>
    </Box>
  );
};

export default MainPageCollectionTab;
