import React from 'react';
import { Flex, Box, Heading, Text } from 'rebass';

import CatalogProducts from '../catalog/CatalogProducts';

const MainPageFeaturedCollectionBlock = ({
  block,
  products,
  skip = 0,
  cartUrl,
}) => {
  const { name, description, limit = 3 } = block;
  return (
    <Box pt={3} mx="auto">
      <Heading
        as="h3"
        fontSize={[30, 36, 42]}
        sx={{ textTransform: 'capitalize', textShadow: '0 0 3px #323232' }}
        textAlign="center"
      >
        {name}
      </Heading>
      <Flex flexWrap="wrap">
        <CatalogProducts
          products={products}
          limit={limit || 3}
          skip={skip}
          cartUrl={cartUrl}
        />
      </Flex>
    </Box>
  );
};

export default MainPageFeaturedCollectionBlock;
